<script>
import CompanyContainer from '@/components/CompanyContainer.vue';
import TopBar from '@/components/TopBar.vue';

export default {
  name: "HomePage",
  components: {
    TopBar,
    CompanyContainer
  },
  data() {
    return {
      isDragging: false,
      startX: 0,
      scrollStart: 0
    };
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    // Companies Scroll Content // 

    // Button Scroll Methods
    scrollLeft() {
      const container = this.$refs.companiesScrollContent;
      container.scrollBy({
        left: -620, // Adjust scroll distance
        behavior: 'smooth'
      });
    },
    scrollRight() {
      const container = this.$refs.companiesScrollContent;
      container.scrollBy({
        left: 620, // Adjust scroll distance
        behavior: 'smooth'
      });
    },

    // Drag Scroll Methods for Mouse
    startDrag(event) {
      this.isDragging = true;
      this.startX = event.pageX - this.$refs.companiesScrollContent.offsetLeft;
      this.scrollStart = this.$refs.companiesScrollContent.scrollLeft;
    },
    onDrag(event) {
      if (!this.isDragging) return;
      event.preventDefault(); // Prevent text selection
      const x = event.pageX - this.$refs.companiesScrollContent.offsetLeft;
      const walk = (x - this.startX) * 1.5; // Adjust scroll speed (multiplier)
      this.$refs.companiesScrollContent.scrollLeft = this.scrollStart - walk;
    },
    stopDrag() {
      this.isDragging = false;
    },

    // Drag Scroll Methods for Touch
    startTouch(event) {
      this.isDragging = true;
      this.startX = event.touches[0].pageX - this.$refs.companiesScrollContent.offsetLeft;
      this.scrollStart = this.$refs.companiesScrollContent.scrollLeft;
    },
    onTouchMove(event) {
      if (!this.isDragging) return;
      const x = event.touches[0].pageX - this.$refs.companiesScrollContent.offsetLeft;
      const walk = (x - this.startX) * 2; // Adjust scroll speed (multiplier)
      this.$refs.companiesScrollContent.scrollLeft = this.scrollStart - walk;
    },
    stopTouch() {
      this.isDragging = false;
    },

    // Other //

    handleResize() {
      const root = document.documentElement;
      const targetMaxWidth = (window.innerWidth > 1400) ? 1400 : window.innerWidth;
      root.style.setProperty("--max-width", targetMaxWidth + 'px');
    },
    handleScroll() {
      const scrollPosition = window.scrollY || window.pageYOffset;
      // const root = document.documentElement;
      if (scrollPosition > 1100 && scrollPosition < 2000) {

        // root.style.setProperty("--background-color", 'var(--brand-color)');
        // root.style.setProperty("--default-text-color", '#fff');
      } else {

        // root.style.setProperty("--background-color", '#fff');
        //root.style.setProperty("--default-text-color", 'var(--default-text-color)');
      }
    }
  }
};
</script>

